import Bmob from "hydrogen-js-sdk";
import unicard from '@/components/uni-card/uni-card.vue'
import uninumberbox from '@/components/uni-number-box/uni-number-box.vue'
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'

let uid;
let that;
export default {
	components: {
		unicard,
		faIcon,
		uninumberbox,
		uniNavBar,
		uniIcon
	},
	data() {
		return {
			products: [],
			user: uni.getStorageSync("user"),
			nums:[], // 多规格时的数量
			_sumNum:0,
		}
	},

	onLoad(options) {
		this.products = []
		that = this
		uni.removeStorageSync("is_option")
		uid = uni.getStorageSync("uid")

		if (options.id) {
			uni.showLoading({title:"加载中..."})
			const query = Bmob.Query('Goods');
			if (options.type == "false") {
				query.equalTo("objectId", "==", options.id);
			} else {
				query.equalTo("productCode", "==", options.id)
			}
			query.equalTo("userId", "==", uid);
			query.find().then(res => {
				console.log(res)
				if (res[0].status == -1) {
					uni.showToast({
						title: "该产品已删除",
						icon: "none"
					})
				} else {
					res[0].num = 1;
					res[0].total_money = 1 * res[0].costPrice;
					res[0].really_total_money = 1 * res[0].costPrice;
					res[0].modify_retailPrice = res[0].costPrice;
					this.products = res;
				}
				uni.hideLoading()
			})
		} else {
			this.products = uni.getStorageSync("products");

			let key = 0;
			for (let item of uni.getStorageSync("products")) {
				
				if(key == uni.getStorageSync("products").length-1){
					this.products = [].concat.apply([],this.products)
				}
				key += 1;
			}

		}

	},

	methods: {
		//扫码
		scanGoods() {
			uni.scanCode({
				success(res) {
					uni.showLoading({title:"加载中..."})
					let result = res.result;
					let array = result.split("-");

					const query = Bmob.Query('Goods');
					if (array[1] == "false") {
						query.equalTo("objectId", "==", array[0]);
					} else {
						query.equalTo("productCode", "==", array[0])
					}
					query.equalTo("userId", "==", uid);
					query.find().then(res => {
						console.log(res)
						if (res[0].status == -1) {
							uni.showToast({
								title: "该产品已删除",
								icon: "none"
							})
						} else {
							for (let item of res) {
								item.num = 1;
								item.total_money = 1 * item.costPrice;
								item.really_total_money = 1 * item.costPrice;
								item.modify_retailPrice = item.costPrice;
							}
							that.products = that.products.concat(res);
						}
						uni.hideLoading()
					})
				},
				fail(res) {
					uni.showToast({
						title: '未识别到条形码',
						icon: "none"
					})
				}
			})
		},

		//头部确定点击
		confrim_this() {
			uni.navigateTo({
				url: "/pages/common/good_confrim/good_enter/good_enter"
			})
		},

		//数量改变
		handleNumChange($event, index) {
			//console.log($event,index)
			this.products[index].num = Number($event)
			this.products[index].total_money = Number($event) * Number(this.products[index].modify_retailPrice)
			this.products[index].really_total_money = Number($event) * Number(this.products[index].really_total_money)
			uni.setStorageSync("products", this.products)
		},
		
		//多类型产品数量改变
		handleModelNumChange($event, index,key,item){
			that.nums[key] = Number($event)
			
			for(let model of this.products[index].models){
				if(model.id == item.id){
					model.num = Number($event)
				}
			}
			
			this.products[index].num = that._sumNum
			this.products[index].total_money = that._sumNum * Number(this.products[index].modify_retailPrice)
			this.products[index].really_total_money = that._sumNum * Number(this.products[index].really_total_money)
			uni.setStorageSync("products", this.products)
		},

		//删除点击
		handleDel(index) {
			console.log(index)
			if (this.products.length == 1) {
				uni.showToast({
					title: "最少选择一个产品",
					icon: "none"
				})
			} else {
				this.products.splice(index, 1);
				uni.setStorageSync("products", this.products)
			}
		},

		//实际价格输入
		getrealprice($event, index) {
			this.products[index].modify_retailPrice = $event.target.value
			this.products[index].total_money = this.products[index].num * Number($event.target.value)
			uni.setStorageSync("products", this.products)
			//console.log($event,index)
		},

	}
}
